import {Container, Row, Col, Button} from 'react-bootstrap'

function importAll(r) {
  return r.keys().map(r);
}

function shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}

function Home() {
  const images = shuffle(importAll(require.context('../images', false, /instagram_\d+\.(png|jpe?g|svg)$/)))

  return (
    <>
      {images.map(image => <img className="instagram-pic" src={image.default}/>)}
      <Container className="info-container">
        <h2 className="text-center">TAP | ATX</h2>
        <h4 className="text-center">Taiwanese American Professionals - Austin Chapter</h4>
        <Row>
          <Col className="text-center">
            <Button>Join Our Mailing List</Button>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Home;
